/* Base Styles */
body {
    margin: 0;
    font-family: 'Inter', sans-serif;
    background-color: #FFFFD5; /* Highlight color */
    color: #3A3A3A; /* Text color */
  }
  
  /* Smooth transitions for a modern feel */
  * {
    transition: all 0.3s ease-in-out;
  }
  
  /* Header styles */
  header {
    background-color: #6A6A4B; /* Primary color */
    padding: 20px;
    text-align: center;
    color: #ffffff; /* White text */
    font-size: 1.5rem;
  }
  
  /* Buttons */
  button {
    background-color: #6A6A4B; /* Primary color */
    color: #ffffff; /* White text */
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 1rem;
  }
  
  button:hover {
    background-color: #3A3A3A; /* Darker color for hover */
  }
  
  /* Forms */
  form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  input, select, textarea {
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #3A3A3A; /* Text color */
    border-radius: 5px;
  }
  
  /* Cards */
  .card {
    background-color: #ffffff; /* White background */
    border: 1px solid #3A3A3A; /* Text color for border */
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    margin: 10px;
    transition: transform 0.2s ease-in-out;
  }
  
  .card:hover {
    transform: scale(1.02);
  }
  
  /* Gradients */
  .gradient-text {
    background: linear-gradient(90deg, #6A6A4B, #FFFFD5);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  